:root {
  --dark-purple: #100633;
  --purple: #1f0b6b;
  --white: #fff;
  --light-grey: #f2f2f2;
  --light-grey1: #d2d6dc;
  --pink: #fa3d66;
  --orange: #ffbc22;
  --box-shadow: 1px 1px 5px 1px rgba(82, 81, 85, 0.25);
  --ease-out: 0.25s ease-in-out;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: var(--dark-purple);
  background-size: 100% 400%;
  background-position: 100% 0%;
  transition: all 1s;
  box-sizing: border-box;
  font-family: 'Poppins', arial, sans-serif;
}

header {
  margin: 10px auto 20px auto;
  text-align: left;
  max-width: 1575px;
}

a {
  color: #fa3d66;
  text-decoration: none;
}

a:link {
  color: var(--pink);
  text-decoration: none;
}

p {
  line-height: 1.5;
}

.description {
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  line-height: 1.7;
}

.searchContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 18px;
  transition: all 1s;
}

#searchbar {
  font-size: 1.2em;
  width: 500px;
  height: 50px;
  text-align: center;
  border: none;
  border-radius: 15px;
  transition: all 1s;

  border: 1px solid rgba(0, 0, 0, 0.24);
}

.twitter-button {
  text-align: right;
  margin-bottom: 24px;
}

#contributions-number {
  color: var(--pink);
  transition: 2.8s;
  font-weight: bold;
  font-size: 36px;
}

.button {
  color: var(--white) !important;
  background: linear-gradient(90deg, var(--pink) 0%, var(--orange) 100%);
  border: none;
  padding: 16px 20px;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: 900;
  cursor: pointer;
  margin-bottom: 1rem;
  -webkit-box-shadow: 1px 1px 5px 1px rgba(82, 81, 85, 0.25);
  -moz-box-shadow: 1px 1px 5px 1px rgba(82, 81, 85, 0.25);
  box-shadow: 1px 1px 5px 1px rgba(82, 81, 85, 0.25);
}

.container {
  max-width: 1614px;
  margin: auto;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
}
.column-left {
  text-align: left;
}

.column-left > h1,
.column-left > h2 {
  font-weight: 900;
  line-height: 1;
}

.column-left > p {
  opacity: 50%;
  max-width: 60ch;
}

.column-left > * {
  margin-bottom: 1.5rem;
}

.column-right,
.column-left {
  margin-inline: clamp(10%, 2vw, 20rem);
}

.column-right {
  text-align: right;
}

.column-double {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
}

footer {
  height: 50px;
  text-align: center;
  line-height: 50px;
  letter-spacing: 2px;
  margin: 16px;
}

/* Card
------ */
.contact i {
  color: var(--pink);
}

.contact a {
  margin-right: 0.5rem;
}

.about {
  background-color: var(--white);
  line-height: 1.7;
  font-size: 1rem;
}

.resources p {
  font-weight: bold;
  font-size: 1.5rem;
}

.card p {
  padding: 8px 24px 8px 24px;
}

.card {
  display: flex;
  flex-direction: column;
  margin: 18px;
  padding: 2rem;
  border-radius: 10px;
  width: 500px;

  transition: 0.3s ease-in;
  border: 1px solid rgba(0, 0, 0, 0.126);
}

.card:hover {
  transform: translateY(-5px);
  border-color: var(--dark-purple);
}

.name {
  background-color: var(--white);
  font-weight: 900;
  font-size: 20px;
}

.contact {
  background-color: var(--white);
  border-bottom: 1px solid var(--dark-purple);
  margin-bottom: 8px;
}

.resources {
  background-color: var(--white);
  padding-right: 25px;
  border-radius: 0 0 5px 5px;
  word-break: break-word;
}

.resources > ul {
  padding-left: 3rem;
}

.resources > ul > li {
  line-height: 1.5;
}

.resources > ul > li:not(:nth-child(3)) {
  padding-bottom: 1rem;
}

.resources > ul > li a:hover,
.contact > a:hover {
  filter: brightness(2);
}

/* Night Mode Code */

.night .contact i {
  color: var(--orange);
}

.night .container {
  color: var(--light-grey1);
}

.night h1 {
  color: var(--orange);
}

.night .card {
  border-color: var(--orange);
}

.night .card:hover {
  background-color: var(--orange);
}

.toggle-box-label-left:empty {
  margin-left: -10px;
}

body.night {
  background-position: 100% 100%;
  background-color: var(--dark-purple);
}

.night .name {
  color: var(--light-grey1);
  background-color: var(--purple);
}

.night .contact {
  background-color: var(--purple);
  border-bottom: 1px solid var(--orange);
}

.night .about {
  color: var(--light-grey1);
  background-color: var(--dark-purple);
}

.night .resources {
  color: var(--light-grey1);
  background-color: var(--dark-purple);
}

.night .resources:hover {
  color: var(--pink);
  background-color: var(--purple);
}

.night #searchbar {
  background-color: var(--dark-purple);
  color: var(--light-grey1);
}

.toggle-box-label-left:before,
.toggle-box-label-left:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /*transition*/
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
  outline: none;
}

.toggle-box input[type='checkbox'],
.toggle-box input[type='checkbox']:active {
  height: 0;
  width: 0;
  opacity: 0;
  border: none;
  outline: none;
}

.toggle-box label {
  display: inline-block;
  position: relative;
  padding: 0px;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  color: rgba(149, 149, 149, 0.51);
  font-weight: normal;
  left: 9px;
  top: 5px;
}

.toggle-box-label-left:before {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  line-height: 34px;
  text-indent: 40px;
  height: 16px;
  width: 16px;
  margin: 4px;
  /*border-radius*/
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  right: 26px;
  bottom: 0px;
  background: var(--orange);
  transform: rotate(-45deg);
  box-shadow: 0 0 10px var(--white);
}

.toggle-box-label-left:after {
  content: '';
  display: inline-block;
  width: 40px;
  height: 24px;
  /*border-radius*/
  -webkit-border-radius: 16px;
  -moz-border-radius: 16px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.15);
  vertical-align: middle;
  margin: 0 10px;
  border: 2px solid var(--orange);
}

.toggle-box input[type='checkbox']:checked + .toggle-box-label-left:before {
  right: 17px;
  box-shadow: 5px 5px 0 0 var(--white);
  background: transparent;
}

.toggle-box input[type='checkbox']:checked + .toggle-box-label-left:after {
  background: rgba(0, 0, 0, 0.15);
  border: 2px solid var(--white);
}

.toggle-box input[type='checkbox'] + .toggle-box-label-left {
  color: rgba(250, 250, 250, 0.51);
  font-weight: bold;
}

.toggle-box input[type='checkbox']:checked + .toggle-box-label-left {
  color: rgba(149, 149, 149, 0.51);
  font-weight: normal;
}

.toggle-box input[type='checkbox']:checked + .toggle-box-label-left + .toggle-box-label {
  color: rgba(250, 250, 250, 0.51);
  font-weight: bold;
}

@media screen and (min-width: 800px) {
  .column {
    flex: 1;
  }
  .double-column {
    flex: 2;
  }
}
